@import "helper/variables";
@import "helper/media";
//@import "helper/mixin"
// RESET

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, button, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  outline: none;
  vertical-align: baseline;
  box-sizing: border-box; }

main, article, aside, details, figcaption, figure,
footer, header, menu, nav, section {
  display: block; }
html,
body {
  height: 100%; }
body {
  color: $colorBlack;
  font: 1rem/1.75rem $mainFont;
  background-color: $colorBlack; }

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-start;
  opacity: 0.1;
  @include transition-long;
  &.ready-load {
    opacity: 1; } }
#main {
 }  //padding-top: 100px

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 1.25rem;
  font-weight: 600; }

h1,
.h1 {
  font-size: 2.5rem;
  line-height: 56px; }

h2,
.h2 {
  font-size: 1.75rem;
  line-height: 2.25rem; }

h3,
.h3 {
  font-size: 1.25rem;
  line-height: 1.75rem; }

h4,
.h4 {
  font-size: 1rem;
  line-height: 26px; }

h5,
.h5 {
  font-size: 17px;
  line-height: 1.5rem; }

h6,
.h6 {
  font-size: 1rem;
  line-height: 1.25rem; }

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: $colorBlack;
  text-decoration: none; }

h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover {
  text-decoration: underline; }

a {
  text-decoration: none;
  color: $colorBlack;
  outline: none;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border-top: 1px solid $colorBlack; }
  &:hover {
    color: $colorRed;
    &:before {
      border-color: $colorRed; } }
  //&:visited
  //  color: $colorRed
  //  &:before
  //    border-color: $colorRed
  &.sub-link {
    &:before {
      border-top: 1px dotted $colorBlack; } }
  &:hover {
    color: $colorRed;
    &:before {
      border-color: $colorRed; } }
  //&:visited
  //  color: $colorRed
  //  &:before
 }  //    border-color: $colorRed
input,
a,
button {
  outline: none; }

ul, p, ol {
  padding: 0;
  margin-bottom: 1.25rem; }

p:empty {
  display: none; }



ul {
  list-style: none;
  li {
    margin: 0 0 10px;
    padding-left: 25px;
    &:before {
      content: "â€”";
      margin-right: .5rem; } }
  ul {
    padding: 1.25rem 0 1px;
    margin: 0;
    li {
      margin-bottom: 10px;
      padding-left: 25px;
      display: flex;
      align-items: center;
      &:before {
        content: "";
        width: .25rem;
        height: .25rem;
        display: inline-block;
        border-radius: 50%;
        background-color: $colorBlack;
        margin-right: .5rem; } } }
  ol {
    padding: 1.25rem 0 1px;
    margin: 0; } }
ol {
  list-style: decimal;
  li {
    margin: 0 0 15px 31px;
    padding: 0;
    &:before {
      display: none; }
    ol {
      padding: 1.25rem 0 1px;
      margin: 0;
      list-style: lower-alpha;
      li {
        margin: 0 0 15px 31px; } }
    ul {
      padding: 1.25rem 0 1px;
      li {
        padding-left: 25px;
        margin-left: 0;
        &:before {
          display: inline-block; } } } } }


img {
  border-style: none;
  outline: none;
  max-width: 100%;
  object-fit: cover;
  height: auto; }

input, a, textarea, button, .slick-slide {
  outline: none; }

strong {
  font-weight: 600; }
// table
table {
  border-collapse: collapse;
  border-spacing: 0;
  border: 0;
  width: 100%;
  margin-bottom: 30px;
  tr {
    &:hover {
      td, th {
        background-color: $colorWhite; } } }
  td, th {
    border-bottom: 1px solid $colorBlackTransparent;
    border-top: 1px solid $colorBlackTransparent;
    font-size: 14px;
    line-height: 1.5rem;
    color: $colorBlack;
    font-weight: 400;
    padding: 14px 1rem;
    text-align: right;
    background-color: transparent;
    @include transition;
    @include mediaMaxWidthTabletPort {
      line-height: 1rem;
      font-size: 14px;
      padding: 0; }
    @include mediaMaxWidthTabletPortPort {
      line-height: 14px;
      font-size: 10px; }
    &:first-child {
      text-align: left; } }
  th {
    padding: 1rem;
    font-weight: 600;
    @include mediaMaxWidthTabletPort {
      line-height: 1.25rem;
      font-size: 1rem;
      background: none; }
    @include mediaMaxWidthTabletPortPort {
      line-height: 14px;
      font-size: .75rem; } } }
//Special Text Elements
sup,
.sup {
  font-size: .75rem;
  position: relative;
  top: -.25rem; }

blockquote,
blockquote {
  margin-left: 30px;
  padding-left: 1rem;
  font-size: 1rem;
  line-height: 2rem;
  font-style: italic;
  margin-bottom: 30px;
  border-left: .125rem solid $colorBlack; }

q,
.q {
  quotes: "\00ab" "\00bb";
  margin-bottom: 1.25rem;
  display: block;
  q {
    quotes: "\201e" "\201c"; } }




