@import "../helper/variables";
@import "../helper/media";
// SLICK-SLIDER
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: pan-y;
  &:hover {
    .slick-next,
    .slick-prev,
    .slick-dots {
      opacity: 1;
      &.slick-disabled {
        opacity: 0.5; } } }
  &.image-slider,
  &.img-carousel {
    overflow-x: hidden; }
  .slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    width: 100%;
    margin: 0;
    padding: 0;
    &:focus {
      outline: none; }
    &.dragging {
      cursor: hand; } }
  .slick-track,
  .slick-list {
    transform: translate3d(0, 0, 0); }
  .slick-track {
    position: relative;
    top: 0;
    left: 0;
    &:before,
    &:after {
      display: table;
      content: ''; }
    &:after {
      clear: both; } }
  .slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    margin-bottom: 0;
    transition: all 5s ease-in-out!important;
    &.slick-current {
      position: relative;
      z-index: 2; }
    img {
      display: block;
      width: 100%; }
 }    //navigation
  .slick-next,
  .slick-prev {
    position: absolute;
    width: 60px;
    height: 60px;
    opacity: 0;
    cursor: pointer;
    font-size: inherit;
    z-index: 3;
    padding: 0;
    top: 50%;
    @include transform-y;
    background-color: transparent;
    border: .125rem solid transparent;
    @include transition;
    .arrow {
      display: flex;
      height: 100%;
      width: 100%;
      align-items: center;
      justify-content: center;
      color: $colorRed;
      background-color: $colorWhite;
      border: .125rem solid $colorWhite;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      @include transition; }

    &:hover {
      border-color: $colorRed;
      .arrow {
        color: $colorWhite;
        background-color: $colorRed;
        border-color: $colorWhite; } }
    &:hover {
      background-color: $colorRed; } }
  .slick-prev {
    left: 0;
    right: auto; }
  .slick-next {
    left: auto;
    right: 0; }
  .slick-dots {
    position: absolute;
    bottom: 1.25rem;
    left: 50%;
    display: flex;
    justify-content: center;
    width: 100%;
    opacity: 0;
    @include transform-x;
    margin: 0;
    padding: 0;
    list-style: none;
    li {
      width: auto;
      height: auto;
      margin: 0 0 0 7px;
      padding: 0;
      border-radius: 50%;
      @include transition;
      &:before {
        display: none; }
      &:first-child {
        margin-left: 0; }
      &.slick-active,
      &:hover {
        button {
          border: 0;
          padding: 0;
          background-color: $colorRed; } } }
    button {
      float: left;
      text-indent: -9999px;
      width: .5rem;
      height: .5rem;
      padding: 0;
      margin: 0;
      border: 0;
      border-radius: 50%;
      background-color: $colorBlack;
      @include transition;
      cursor: pointer; } } }
//SLIDER ANIMATION
.ready-load {
  .slick-slide {
    transform: scale(1);
    &.slick-current {
      transform: scale(1.1); } } }

//site-slider
.site-slider {
  margin-bottom: 1.25rem;
  .item-slide {
    height: 680px;
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    @include mediaMaxWidthTabletPortPort {
      height: 280px; } } }
