@import "../helper/variables";
@import "../helper/media";
#header {
  background-color: $colorBlack;
  height: 100px;
  top: 0;
  left: 0;
  width: 100%;
  position: fixed;
  display: flex;
  z-index: 20;
  .container {
    position: unset;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  li,a {
    &:before {
      display: none; } }
  // logo
  .logo {
    height: 57px;
    @include mediaMaxWidthTabletPort {
      height: 30px; }
    a {
      height: 100%;
      display: block; }
    img {
      height: 100%;
      width: auto;
      @include transition; } }

  // MOBILE MENU ACTIVE
  &.menu-active {
    .navigation {
      right: 0; } } }
//menu opener icon
.menu-opener-js {
  display: none;
  position: fixed;
  width: 2.5rem;
  height: 2.5rem;
  top: 25px;
  right: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .bar {
    width: 100%;
    display: block;
    background: $colorWhite;
    height: .125rem;
    margin: .125rem 0;
    clear: both;
    @include transition; }
  @include mediaMaxWidthTabletPort {
    display: flex;
    z-index: 10; }
  &.active {
    .bar {
      display: none;
      &:nth-child(1),
      &:nth-child(3) {
        display: block;
        margin: 5px 0 0;
        transform: rotate(45deg); }
      &:nth-child(3) {
        transform: rotate(-45deg); } } } }
//TOP-LINKS
.top-links {
  display: flex; }
//navigation
.navigation {
  @include transition;
  @include mediaMaxWidthTabletPort {
    right: -100%;
    width: 100%;
    background: $colorBlackTransparent;
    top: 100%;
    position: absolute; }
  nav {
    ul {
      padding: 0;
      list-style: none;
      margin: 0;
      display: flex;
      flex-direction: row;
      @include mediaMaxWidthTabletPort {
        flex-direction: column; } }
    li {
      margin: 0 0 0 25px;
      &:first-child {
        margin-left: 0; }
      @include mediaMaxWidthTabletPort {
        margin: 0; } }
    a {
      color: $colorWhite;
      text-decoration: none;
      text-transform: uppercase;
      padding: 0;
      display: inline-block;
      vertical-align: top;
      font-weight: 300;
      font-size: 1rem;
      line-height: 30px;
      @include transition;
      @include mediaMaxWidthTabletPort {
        display: block;
        padding: 10px 1.25rem; }
      &:hover {
        color: $colorRed; } } }


  //drop-down
  .drop-down-icon {
    display: inline-block;
    position: absolute;
    width: 1.25rem;
    height: 1.25rem;
    vertical-align: top;
    right: -1rem;
    top: .25rem;
    background-color: transparent;
    &:before {
      border: 5px solid transparent;
      border-top: 5px solid $colorWhite;
      content: "";
      width: 0;
      height: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: 3px;
      @include transform; }

    @include mediaMaxWidthTabletPort {
      position: absolute;
      right: 0;
      top: 0;
      height: 2.5rem;
      width: 2.5rem;
      background-color: $colorRed;
      z-index: 1;
      &:before {
        border-top-color: $colorWhite; } } }
  .dropdown {
    position: relative;
    &:hover {
      .dropdown-menu {
        @include mediaMinWidthTabletPort {
          display: block;
          margin: 0;
          max-height: 999px; } } } }
  .dropdown-menu {
    border: 0;
    position: absolute;
    top: 100%;
    padding: 0;
    float: none;
    margin: 0;
    background: none;
    border-radius: 0;
    overflow: hidden;
    max-height: 0;
    min-width: 250px;
    @include transition;
    &.active {
      max-height: 9999px; }
    @include mediaMaxWidthTabletPort {
      position: relative;
      top: 0;
      display: block; }
    a {
      padding: 5px 22px;
      display: block;
      background-color: $colorBlack;
      @include mediaMaxWidthBigDesktop {
        padding: 5px 15px; }
      &:hover {
        background-color: $colorWhite;
        text-decoration: none; } } } }


