@import "../helper/variables";
@import "../helper/media";
//  FORM STYLE
form {
  margin-bottom: 25px;
  input[type="text"],
  input[type="file"],
  input[type="password"],
  input[type="tel"],
  input[type="email"],
  input[type="search"],
  input[type="number"],
  textarea {
    border: 1px solid $colorBlackLight;
    padding: 10px 17px;
    font-size: 1rem;
    width: 100%;
    border-radius: 0;
    line-height: 1.75rem;
    background-color: transparent;
    margin-bottom: 1.25rem;
    font-family: $mainFont;
    box-sizing: border-box;
    @include transition;
    &:focus {
      border-color: $colorBlack;
      background-color: $colorWhite; }
    &:disabled {
      border-color: $colorBlackTransparent; }
    &.error {
      border-color: $colorRed; } }
  textarea {
    width: 100%;
    height: 200px;
    resize: none; }
  label.error {
    display: none!important; } }
//form-field
.form-field {
  margin-bottom: 25px;
  .form-field__label {
    display: block;
    height: 100%;
    .title,
    .hint {
      display: block;
      font-size: 1rem;
      line-height: 1.75rem; }
    .title {
      cursor: pointer;
      color: $colorBlack;
      @include transition;
      &:hover {
        color: $colorRed; } }
    .hint {
      font-size: 14px;
      line-height: 1.5rem;
      margin-bottom: 10px;
      color: $colorBlackLight; }
    input,
    textarea {
      margin: 0; } } }

// CHECKBOX STYLING
.form-checkbox,
.form-radio {
  position: relative;
  font-size: 14px;
  input[type='checkbox'],
  input[type='radio'] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  input[type='checkbox'],
  input[type='radio'] {
    &:checked + .check-label:before,
    &:checked + .check-label:before {
      opacity: 1; }
    &:checked + .check-label:after,
    &:checked + .check-label:after {
      border-color: $colorRed; }
    &:disabled + .check-label,
    &:disabled + .check-label {
      color: $colorBlackLight;
      &:after {
          border-color: $colorBlackTransparent; }
      &:hover {
        &:after {
          border-color: $colorBlackTransparent; } } } }

  .check-label {
    position: relative;
    padding-left: 30px;
    display: block;
    cursor: pointer;
    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: .125rem;
      @include transition; }
    &:before {
      width: 14px;
      height: 14px;
      background: $colorBlackLight;
      opacity: 0;
      top: 6px;
      left: .25rem; }
    &:after {
      width: 1rem;
      height: 1rem;
      border-radius: 3px;
      border: .125rem solid $colorBlackLight; }
    &:hover {
      &:after {
        border-color: $colorRed; } } } }


//CHECKBOX-STYLING
.form-checkbox {
  .check-label {
    &:before {
      content: "\f00c";
      font-family: FontAwesome;
      background: $colorRed;
      color: $colorWhite;
      width: 1rem;
      height: 1rem;
      top: .25rem;
      left: .125rem;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1rem; } } }
//RADIO-STYLING
.form-radio {
  .check-label {
    &:before,
    &:after {
      border-radius: 50%; }
    &:before {
      width: .75rem;
      height: .75rem;
      background: $colorRed;
      opacity: 0;
      top: 7px;
      left: 5px;
      @include transition; } } }





// SELECT STYLING
.jcf-focus {
  border-color: $colorBlack; }

.jcf-select,
select {
  text-align: left;
  height: 50px;
  width: 100%;
  cursor: pointer;
  display: inline-block;
  border: 1px solid $colorBlack;
  position: relative;
  font-family: $mainFont;
  &.jcf-drop-active {
    .jcf-select-text {
      background-color: $colorWhite; }
    .jcf-select-opener {
      &:after {
        top: 37px;
        transform: rotate(-45deg); } } }

  &.check-list,
  &.jcf-select-check-list {
    .jcf-select-drop-content {
      li {
        .jcf-option {
          padding-left: 2.75rem;
          position: relative;
          &:before,
          &:after {
            content: '';
            position: absolute;
            left: 15px;
            top: 13px;
            @include transition; }
          &:before {
            background: $colorBlackLight;
            opacity: 0;
            content: "\f00c";
            font-family: FontAwesome;
            background: $colorRed;
            color: $colorWhite;
            width: 1rem;
            height: 1rem;
            top: 15px;
            left: 17px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1rem; }
          &:after {
            width: 1rem;
            height: 1rem;
            border-radius: 3px;
            margin-right: 11px;
            border: .125rem solid $colorBlackLight; }
          &:hover {
            color: $colorRed;
            &:after {
              border-color: $colorRed; } }
          &.jcf-selected {
            &:before {
              opacity: 1; }
            &:after {
              border-color: $colorRed; } } } } } }
  &.jcf-disabled {
    opacity: .3; }
  .jcf-select-opener {
    width: 2.75rem;
    height: 2.75rem;
    background-color: transparent;
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;

    &:after {
      display: block;
      position: absolute;
      right: 0;
      bottom: 0;
      border-right: 1px solid $colorBlack;
      border-top: 1px solid $colorBlack;
      text-align: center;
      content: "";
      color: $colorWhite;
      line-height: 60px;
      width: 7px;
      height: 7px;
      margin: -14px 0 0;
      text-indent: 0 !important;
      left: 1rem;
      top: 33px;
      transform: rotate(135deg);
      @include transition; } }
  .jcf-select-text {
    line-height: 2.75rem;
    color: $colorBlack;
    font-size: 14px;
    width: 100%;
    padding: 0 60px 0 17px;
    background-color: transparent;
    display: inline-block;
    @include transition; } }
.jcf-select-drop {
  position: absolute;
  top: calc(100% + .125rem);
  left: -1px;
  z-index: 3; }
.jcf-select-drop-content {
  border: 0;
  box-shadow: 0 .5rem .75rem rgba(2, 21, 51, 0.1);
  ul {
    margin: 0;
    padding: 0;
    list-style: none; }

  li {
    margin: 0;
    padding: 0;
    &:before {
      display: none; }
    .jcf-option {
      padding: 10px 17px;
      font-size: 14px;
      line-height: 1.75rem;
      color: $colorBlack;
      background-color: $colorWhite;
      display: block;
      cursor: pointer;
      @include transition;
      &:hover,
      &.jcf-selected {
        background-color: $colorBlack; } } } }
//placeholder
input::-moz-placeholder {
  color: $colorBlack;
  opacity: .7; }

input::-webkit-input-placeholder {
  color: $colorBlack;
  opacity: .7; }

input:-ms-input-placeholder {
  color: $colorBlack;
  opacity: .7; }

textarea::-moz-placeholder {
  color: $colorBlack;
  opacity: .7; }

textarea::-webkit-input-placeholder {
  color: $colorBlack;
  opacity: .7; }

textarea:-ms-input-placeholder {
  color: $colorBlack;
  opacity: .7; }
//search-form
.search-form {
  position: relative;
  .search-text {
    width: 100%;
    padding-right: 60px; }
  .search-button {
    position: absolute;
    background-color: transparent;
    border: 0;
    top: 0;
    right: 0;
    color: $colorBlack;
    opacity: .5;
    font-size: 22px;
    @include transition;
    &:hover {
      opacity: 1;
      color: $colorRed; } } }

@-webkit-keyframes autofill {
  to {
    background: transparent; } }

input:-webkit-autofill {
  -webkit-animation-name: autofill;
  -webkit-animation-fill-mode: both; }
