@import "../helper/variables";
@import "../helper/media";
// button-style
.btn,
button,
input[type="submit"],
input[type="reset"] {
  color: $colorWhite;
  font-size: 1rem;
  cursor: pointer;
  line-height: 1.75rem;
  padding: 10px 17px;
  border: 1px solid $colorRed;
  text-decoration: none;
  display: inline-block;
  border-radius: 0;
  outline: none;
  font-weight: 600;
  text-align: center;
  margin-bottom: 1.25rem;
  appearance: none;
  background-color: $colorRed;
  @include transition;
  &:before {
    display: none; }
  &:hover {
    color: $colorWhite;
    text-decoration: none;
    background: $colorRed;
    border-color: $colorRed; }
  &.btn_secondary {
    color: $colorBlack;
    border-color: $colorBlackLight;
    background-color: $colorWhite;
    &:hover {
      color: $colorWhite;
      border-color: $colorBlack;
      background-color: $colorBlack; }
    &:disabled {
      border-color: $colorBlackTransparent;
      color: $colorBlackTransparent; } }
  &:disabled {
    border-color: $colorRed;
    background-color: $colorRed; } }


