/* variables */
// color
$colorBlack: rgba(238, 238, 238, 1);

$colorRed: rgba(187, 15, 30, 1);
$colorRed: rgba(234, 19, 37, 1);
$colorRed: rgba(234, 19, 37, 0.2);

$colorBlack: rgba(2, 21, 51, 1);
$colorBlackLight: rgba(2, 21, 51, 0.6);
$colorBlackTransparent: rgba(2, 21, 51, .2);

$colorWhite: rgba(255, 255, 255, 1);
$colorWhiteLight: rgba(255, 255, 255, .6);
$colorWhiteTransparent: rgba(255, 255, 255, .2);
// font
$mainFont: 'Montserrat', sans-serif;

// mixin

@mixin transition() {
  transition: all 0.2s ease-in-out; }

@mixin transition-long() {
  transition: all 0.7s ease-in-out; }

@mixin transform() {
  transform: translate(-50%, -50%); }

@mixin transform-x() {
  transform: translateX(-50%); }

@mixin transform-y() {
  transform: translateY(-50%); }



