@import "helper/variables";
@import "helper/media";
// basic style
// container
.container {
  width: 1216px;
  max-width: 95%;
  margin: 0 auto; }

//grid
.row {
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  width: calc(100% + 30px);
  margin: 0 -15px; }
.col {
  display: flex;
  flex-direction: column;
  padding: 0 15px;
  box-sizing: border-box;
  @include mediaMaxWidthTabletPortPort {
    margin-bottom: 1.25rem; }
  &.col_1 {
    width: 8.3333%; }

  &.col_2 {
    width: 16.666%; }

  &.col_3 {
    width: 25%; }

  &.col_4 {
    width: 33.3%; }

  &.col_5 {
    width: 41.666667%; }

  &.col_6 {
    width: 50%; }

  &.col_7 {
    width: 58.333333%; }

  &.col_8 {
    width: 66.666667%; }

  &.col_9 {
    width: 75%; }

  &.col_10 {
    width: 83.333333%; }

  &.col_11 {
    width: 91.666667%; }

  &.col_12 {
    width: 100%; }
  @include mediaMaxWidthBigDesktop {
    &.col_big-desktop-2 {
      width: 16.66%; }
    &.col_big-desktop-3 {
      width: 25%; }
    &.col_big-desktop-4 {
      width: 33.3%; }
    &.col_big-desktop-5 {
      width: 41.666667%; }
    &.col_big-desktop-6 {
      width: 50%; }
    &.col_big-desktop-7 {
      width: 58.333333%; }
    &.col_big-desktop-8 {
      width: 66.666667%; }
    &.col_big-desktop-9 {
      width: 75%; }
    &.col_big-desktop-10 {
      width: 83.333333%; }
    &.col_big-desktop-11 {
      width: 91.666667%; }
    &.col_big-desktop-12 {
      width: 100%; } }
  @include mediaMaxWidthTabletPortLand {
    &.col_middle-desktop-2 {
      width: 16.66%; }
    &.col_middle-desktop-3 {
      width: 25%; }
    &.col_middle-desktop-4 {
      width: 33.3%; }
    &.col_middle-desktop-5 {
      width: 41.666667%; }
    &.col_middle-desktop-6 {
      width: 50%; }
    &.col_middle-desktop-7 {
      width: 58.333333%; }
    &.col_middle-desktop-8 {
      width: 66.666667%; }
    &.col_middle-desktop-9 {
      width: 75%; }
    &.col_middle-desktop-10 {
      width: 83.333333%; }
    &.col_middle-desktop-11 {
      width: 91.666667%; }
    &.col_middle-desktop-12 {
      width: 100%; } }
  @include mediaMaxWidthTabletPortLand {
    &.col_desktop-2 {
      width: 16.66%; }
    &.col_desktop-3 {
      width: 25%; }
    &.col_desktop-4 {
      width: 33.3%; }
    &.col_desktop-5 {
      width: 41.666667%; }
    &.col_desktop-6 {
      width: 50%; }
    &.col_desktop-7 {
      width: 58.333333%; }
    &.col_desktop-8 {
      width: 66.666667%; }
    &.col_desktop-9 {
      width: 75%; }
    &.col_desktop-10 {
      width: 83.333333%; }
    &.col_desktop-11 {
      width: 91.666667%; }
    &.col_desktop-12 {
      width: 100%; } }
  @include mediaMaxWidthTabletPort {
    &.col_tab-3 {
      width: 25%; }
    &.col_tab-4 {
      width: 33.3%; }
    &.col_tab-5 {
      width: 41.666667%; }
    &.col_tab-6 {
      width: 50%; }
    &.col_tab-7 {
      width: 58.333333%; }
    &.col_tab-8 {
      width: 66.666667%; }
    &.col_tab-9 {
      width: 75%; }
    &.col_tab-10 {
      width: 83.333333%; }
    &.col_tab-11 {
      width: 91.666667%; }
    &.col_tab-12 {
      width: 100%; } }
  @include mediaMaxWidthTabletPortPort {
    &.col_mob-3 {
      width: 25%; }
    &.col_mob-4 {
      width: 33.3%; }
    &.col_mob-6 {
      width: 50%; }
    &.col_mob-8 {
      width: 66.666667%; }
    &.col_mob-12 {
      width: 100%; } }
  @include mediaMaxWidthMobile {
    &.col_mob-p-12 {
      width: 100%; } } }
//text-align
.text-center {
  text-align: center; }
.text-tab-center {
  @include mediaMaxWidthDesktop {
    text-align: center; } }
.text-left {
  text-align: left; }

.text-right {
  text-align: right;
  @include mediaMaxWidthDesktop {
    align-items: center; } }
//align-center
.align-center {
  align-items: center; }

.align-start {
  align-items: start;
  @include mediaMaxWidthTabletPortPort {
    align-items: center; } }
.align-end {
  align-items: end;
  @include mediaMaxWidthTabletPortPort {
    align-items: center; } }
//justify-content
.content-center {
  justify-content: center; }

.content-between {
  justify-content: space-between; }

.content-start {
  justify-content: start; }

.content-end {
  justify-content: end; }
.content-tab-between {
  @include mediaMaxWidthDesktop {
    justify-content: center; } }
//margin
.margin-b {
  margin-bottom: 2.5rem; }
//padding
.no-padding-b {
  padding-bottom: 0; }

//fancybox
.fancybox-overlay-fixed {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 99999;
  background: rgba(255,255,255,0.9);
  @include mediaMaxWidthMobile {
    z-index: 9999; } }
.fancybox-opened {
  z-index: 99999; }

//site-guide-page
.site-guide-page {
  #header,
  #footer {
    display: none; }
  .site-guide-list {
    display: flex;
    height: 100vh;
    align-items: center;
    .item {
      margin-bottom: 1.25rem; }
    .link {
      display: block;
      text-decoration: none;
      color: $colorRed;
      font-size: 25px;
      line-height: 25px;
      position: relative;
      @include transition;
      left: 0;
      &:before {
        display: none; }
      &:hover {
        left: 15px; } } } }

