//fancybox
.fancybox-next,
.fancybox-prev,
.fancybox-close {
  opacity: 1;
  position: absolute;
  width: 100px;
  height: 100px;
  cursor: pointer;
  z-index: 101;
  text-indent: -9999px;
  background-color: rgba(255,255,255,0.2);
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  @include transform-y;
  @include transition;
  @include mediaMaxWidthMobile {
    background: none; }
  &:after {
    display: block;
    position: relative;
    border-top: .125rem solid $colorWhite;
    border-left: .125rem solid $colorWhite;
    text-align: center;
    content: "";
    left: -5px;
    color: $colorWhite;
    line-height: 29px;
    width: 1.5rem;
    height: 1.5rem;
    text-indent: 0 !important;
    transform: rotate(135deg); }
  &:hover {
    background-color: $colorWhite;
    &:after {
      border-color: $colorRed; } } }
.fancybox-next,
.fancybox-prev {
  border-radius: 50%;
  @include mediaMaxWidthMobile {
    width: 50px;
    height: 100%; } }
.fancybox-prev {
  left: 10px;
  right: auto;
  &:after {
    transform: rotate(-45deg);
    left: 10px; } }
.fancybox-next {
  left: auto;
  right: 10px; }
.fancybox-close {
  top: 0;
  transform: none;
  right: 0;
  background-color: #fff;
  text-decoration: none;
  &:after {
    content: "\f00d";
    color: $colorRed;
    border: 0;
    -webkit-transform: none;
    transform: none;
    font: normal normal normal 50px/100px FontAwesome;
    width: auto;
    height: auto; }
  @include mediaMaxWidthMobile {
    position: fixed; }
  &:hover {
    &:after {
      color: $colorRed; } } }
